<template>
  <div class="hello mint-cell">
    <h2>页面访问</h2>
    <ul>
      <li><router-link :to="{ name: 'search'}">搜索页面</router-link></li>
      <li><router-link :to="{ name: 'consult'}">咨询页面</router-link></li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data() {
    return {
      msg: 'Welcome to Your Vue.js App',
    };
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}


</style>



