<template>
  <div>
    <transition name="fadeIn">
      <div class="popup-mask" v-show="currentValue" @click="maskClick"></div>
    </transition>
    <transition name="popup-slide-bottom">
      <div  class="popup-container popup-bottom" v-show="currentValue">
        <slot></slot>
      </div>
    </transition>
  </div>
</template>

<script type="text/Babel">
  export default {
    name: 'popup',
    data() {
      return {
        currentValue: false,
      };
    },
    props: {
      /* eslint-disable */
      value: {
        type: Boolean,
        default: false
      },
      /* eslint-enable */
    },
    watch: {
      value(val) {
        this.currentValue = val;
      },
    },
    mounted() {
      if (this.value) {
        this.currentValue = true;
      }
    },
    methods: {
      maskClick() {
        this.$emit('input', false);
      },
    },
  };
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  @import "../../assets/style/variables";

  .popup-container {
    width: 100%;
    position: fixed;
    background: #fff;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
    backface-visibility: hidden;
    transition: all .2s ease-out;
  }

  .popup-bottom {
    top: auto;
    right: auto;
    bottom: 0;
    left: 50%;
    transform: translate3d(-50%, 0, 0);
  }

  .popup-mask {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0.5;
    background: #000;
  }

  .popup-slide-bottom-enter,
  .popup-slide-bottom-leave-active {
    transform: translate3d(-50%, 100%, 0);
  }
</style>



