import Vue from 'vue';
import Router from 'vue-router';
import Hello from '@/components/Hello';

/* eslint-disable */
const search = r => require(['@/page/search.vue'], r);
const consult = r => require(['@/page/consult.vue'], r);
/* eslint-enable */

Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello,
    },
    {
      path: '/search',
      name: 'search',
      component: search,
    },
    {
      path: '/consult',
      name: 'consult',
      component: consult,
    },
  ],
});

const titleDic = {
  Hello: '腾爱',
  search: '搜索',
  consult: '咨询',
};

router.beforeEach((to, from, next) => {
  document.title = titleDic[to.name] || document.title;
  next();
});

export default router;



