import {
  UPDATE_SEARCH_RESULT,
} from '../types';
import api from '../../api';

const state = {
  results: [
    {
      name: '赵可醒',
      duty: '主任医师',
      focus: '乳腺外科',
      from: '北京协和医院',
      /* eslint-disable */
      avatar: require('../../assets/images/avatar1.png'),
      skill: '乳腺肿瘤',
      applyNum: '120',
      isConsult: false,
    },
    {
      name: '韩大生',
      duty: '主任医师',
      focus: '乳腺外科',
      from: '北京协和医院',
      /* eslint-disable */
      avatar: require('../../assets/images/avatar2.png'),
      skill: '乳腺肿瘤',
      applyNum: '73',
      isConsult: false,
    },
    {
      name: '陈舒',
      duty: '主任医师',
      focus: '乳腺外科',
      from: '北京协和医院',
      /* eslint-disable */
      avatar: require('../../assets/images/avatar3.png'),
      skill: '乳腺肿瘤',
      applyNum: '12',
      isConsult: true,
    },
  ],
};

const mutations = {
  [UPDATE_SEARCH_RESULT](state, action) {
    state.results = state.results.concat(action.data);
  },
};

const actions = {
  async sendSearch({ commit }, params) {
    if (process.env.NODE_ENV === 'production') {
      return commit(UPDATE_SEARCH_RESULT, {
        data: state.results.map((item) => Object.assign(item)),
      });
    } else {
      var res = await api.getSearchResult(params);
      return commit(UPDATE_SEARCH_RESULT, {
        data: (res.data || []),
      });
    }
  }
};

export default {
  state,
  mutations,
  actions
};



